var _self;
export default {
	data() {
		return {
			loginUsername: '',
			loginPassword: '',
			message: ''
		}
	},
	onLoad() {
		_self=this;
	},
	methods: {
		openRegisterPage() {
			uni.navigateTo({
				url: '../login/register',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		goLogin() {
			let loginUsername = this.loginUsername;
			let loginPassword = this.loginPassword;
			if (!loginUsername) {
				this.message = "用户名不能为空";
				return false;
			}
			if (!loginPassword) {
				this.message = "密码不能为空";
				return false;
			}
			uni.showLoading({
				title: '登录中...',
				mask: false
			});
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				method: 'get',
				data: {
					name: this.loginUsername,
					pw: this.loginPassword
				},
				success: res => {
					
					//这里都是假的，其实并没有在后台判断用户和密码
					if (_self.loginUsername=='ucharts' &&　_self.loginPassword=='123456') {
						let userinfo={
							avatarUrl:'../../static/images/avatar.png',
							name:'uCharts',
							phone:'13888888888',
							email:'admin@ucharts.cn'
						}
						uni.setStorageSync('userinfo', userinfo);
						uni.hideLoading();
						uni.reLaunch({
							url: 'index'
						});
					} else {
						uni.removeStorageSync('userinfo');
						this.message = '用户名或密码错误';
						uni.hideLoading();
					}
				},
				fail: () => {
					uni.hideLoading();
					this.message = "网络连接失败";
				},
				complete: () => {}
			});
		}
	}
}
